<template>
  <div class="page-header app-header auto">
    <div @click='back()' v-show="needBack">
     <img src="../../assets/back.png"/>
    </div>
  </div>
</template>

<script>
  export default {
    title: '底部返回组件',
    data () {
      return {}
    },
    props: {
      title: {
        type: String,
        default: '燃气应用'
      },
      needBack: {
        type: Boolean,
        default: true
      }
    },
    methods: {
      back () {
        // console.log('发送返回事件')
        this.$emit('flag')
      },
      // 关闭新消息响铃（静音）
      mute () {
        HostApp.mute()
      },
    }
  }
</script>
<style scoped>
  .app-header {
    border-radius: 50%;
    position:fixed;
    background: rgba(73, 158, 223, 0.12);
    color: #FFF;
    padding: 8px;
    text-align: center;
    font-size: 1.2em;
    z-index: 10;
    opacity:0.5;
    bottom: 50px;
    margin-top: -20px;
  }
</style>



